@import '~node_modules/modern-normalize/modern-normalize.css';

 *,
 *::before,
 *::after {
   box-sizing: border-box;
 }

body {
  font-family: $main-font, sans-serif;
    font-size: 16px;
    line-height: 1.15;
    color: $main-text;
    background-color: $main-bg;
    margin: 0;

    &.no-scroll {
    overflow: hidden;
}
}

// reset
h1,
h2,
h3,
p {
  margin: 0;
  pointer-events: none;
}

ul {
    list-style: none;
    margin: 0;
      padding: 0;
}

h3 {margin-top: 0;
    margin-bottom: 10px;
}

.link {
    text-decoration: none;
    color: currentColor;
    }

ul {
    list-style: none;
    }

    .list {
        padding-left: 0;
    }